<template>
    <ul>
        <li>
            <router-link :to="{name: 'Home'}">
                Home
            </router-link>
        </li>

        <li>
            <router-link :to="{name: 'About'}">
                About
            </router-link>
        </li>
        <template v-if="!authenticated">
        <li>
            <router-link :to="{name: 'Signup'}">
                Signup
            </router-link>
        </li>
        <li>
            <router-link :to="{name: 'Login'}">
                Login
            </router-link>
        </li>
        </template>
        <template v-if="authenticated">
        <li>
            <router-link :to="{name: 'Run'}">
                Run
            </router-link>
        </li>
        <li>
            {{user.username}}
        </li>
        <li>
            <a href="#" @click.prevent="logOut">
                Logout
            </a>
        </li>
        </template>
        
    </ul>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
export default {
    computed: {
        ...mapGetters({
            authenticated: 'auth/authenticated',
            user: 'auth/user',
        })
    },
    methods: {
        ...mapActions({
            logOutAction: 'auth/logOut'
        }),
        logOut () {
            this.logOutAction();
            this.$router.push({
                    name: 'Home'
            })
        }
    },
}
</script>